    z-index: 0;
}
body{
  overflow-y: scroll;
}
.container{
  width: calc(100% - 60px);
  max-width: 1860px; height: 100%;
  margin: auto;
  padding-left: 30px;
  padding-right: 30px;
}
#app {
  /* font-family: 'Avenir', Helvetica, Arial, sans-serif; */
