}
/* header
------------------------------------------------------------------------------*/
header{
    position: fixed; top: 0; right: 0;
    height: 100%; width: 100%;
    background-color: #000000;
    z-index: 1;
}
nav{
    position: absolute; top: 0; right: 0; height: 100%;
    text-align: right; font-size: 0.5rem;
    width: 4.5rem;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: end;
        -ms-flex-pack: end;
            justify-content: flex-end;
}
nav > ul{
  width: 100%;
}
nav > ul li{
  padding-top: 0.5em;
}
nav ul a{
  color: #ffffff;
  margin-right: 0.5rem;
  line-height: 1.5;
  position: relative;
  display: inline-block;
}
nav ul a:before{
    content: '';
    display: block;
    width: 0;
    height: 1px;
    background-color: #be974f;
    position: absolute;
    top: calc(50% + 0.6em);
    left: 50%;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
}
nav ul a:hover,
nav ul li.active a{
  color: #be974f;
}
nav ul a:hover:before,
nav ul li.active a:before{
  width: 100%; left: 0;
}
nav ul ul{ font-size: 0.4rem; padding-bottom: 0.2rem; background-color: rgba(190,151,79,0.3);
}
nav ul ul a{ color: rgba(190,151,79,0.8)
}
